* {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif; }

.header {
    min-height: 100vh;
    width: 100%;
    background: linear-gradient(rgba(4,9,30,0.7), rgba(4,9,30,0.5)), url('../img/banner1.png') center (center/cover);
    position: relative; }

.container {
    max-width: 80%;
    position: relative; //для Tiny-Slider
    margin: 0 auto; }

nav {
    display: flex;
    padding: 2% 6%;
    justify-content: space-between;
    align-items: center;
    img {
        width: 150px; }
    & .icon-animation {
        transition: 0.5s; /* Время эффекта */;
        &:hover {
            transform: scale(1.1); /* Увеличиваем масштаб */; } } }

.nav-links {
    flex: 1;
    text-align: right;
    ul li {
        list-style: none;
        display: inline-block;
        padding: 8px 12px;
        position: relative;
        a {
            color: #fff;
            text-decoration: none;
            font-size: 13px; }
        &::after {
            content: '';
            width: 0%;
            height: 2px;
            background: #f44366;
            display: block;
            margin: auto;
            transition: 0.5s; }
        &:hover::after {
            width: 100%; } } }

.text-box {
    width: 90%;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    .container {
        background-color: rgba(0, 0, 0, 0.353);
        padding: 40px 0 40px;
        border-radius: 10px; }
    h1 {
        font-size: 35px; }
    p {
        margin: 10px 0 30px;
        font-size: 16px;
        color: #fff; } }

.hero-btn {
    display: inline-block;
    text-decoration: none;
    color: #fff;
    border: 1px solid #fff;
    padding: 12px 34px;
    font-size: 13px;
    border-radius: 5px;
    background: transparent;
    position: relative;
    cursor: pointer;
    &:hover {
        border: 1px solid #f44336;
        background: #f44336;
        transition: 0.6s; } }

nav .fa {
    display: none; }

